.menu-interface-component-inner{
    width: 100%;
    font-size: 0.7rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.menu-interface-component-input-container{
    display: flex;
    flex-direction: column;
    gap: 1rem;

}

.menu-interface-component-input-container input{
    width: 100%;
}

.menu-interface-component-input-container input::placeholder{
    font-size: 0.7rem;
}


.menu-interface-component-input{
    border: none;
    border-bottom: 1px solid gainsboro;
}

.menu-interface-component-bottom{
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-interface-component-bottom button{
    background-color: #2f2f91;
    color: white;  
    padding: 0.3rem 2rem;
}








